import React from "react"
import Layout from "../componets/layout"
import TapMenu from "../componets/tapmenu"
//import { Link } from "gatsby"
const table = {
    borderTop: '2px solid black',
    bordercollapse: "collapse",
    width: 1280
}

// const tbody = {
//     fontSize:14
// }


const td14 = {
  borderBottom : "1px solid #ddd",
  width : 140,
  height : 55,
  textAlign:"center"
}
const td86 = {
    borderBottom : "1px solid #ddd",
    width : 860,
    height : 55,
}
// const td86c = {
//     borderBottom : "1px solid #ddd",
//     width : 860,
//     height : 55,
//     cursor:"pointer"
// }


export default ({children}) =>
{

    return(
        <Layout>
        <div style={{  margin:"auto", width:"100%", textAlign:"center"}}>
            <TapMenu  type={3}/>


            <div style={{ borderBottom :"1px solid #ddd",  margin:"auto",   width: 1280, textAlign:"left" }}>
            <h4 style={{marginTop:40}}> 전체 0 </h4>


        <table style={table}>
            <thead style={{fontWeight:"bold", textAlign:"center" }}>
                <tr>
                    <td style={td14}>    구분    </td>
                    <td style={td86}>    제목    </td>
                    <td style={td14}>    작성일    </td>
                    <td style={td14}>    조회수    </td>
                </tr>
            </thead>



        </table>

        <div style={{marginTop:100,  textAlign:"center"}}>
            등록된 이벤트가 없습니다.
        </div>

        <div style={{marginTop:100}}></div>



        </div>
        </div>
        </Layout>
    )
}


